@use 'sass:map';
@use '@styles/variables';

.landing {
  display: flex;
  align-items: center;
  justify-content: center;
  background: 50% 99% url('@assets/decorative/landing-background.webp') no-repeat;
  background-size: cover;
  padding: 5px;
  min-height: 830px;

  &__text {
    display: none;
  }

  @media (min-width: map.get(variables.$breakpoints, 'tablet')) {
    align-items: normal;

    &__content {
      max-width: map.get(variables.$layout-sizes, 'container');
      margin: 0 auto;
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 65px 128px 15px 137px;
    }

    &__booking-card {
      align-self: flex-start;
    }

    &__text {
      display: block;
      max-width: 297px;
      align-self: flex-end;
      justify-self: flex-end;
      text-align: right;
      margin: 1px 0 0 0;
    }
  }
}
